import './Themes.less';

import {
    Component,
    E,
    block,
} from '../../../utils';

import { Breadcrumbs } from '../../../components';

const b = block('themes');

const example = [
    [' ', b('text')],
    ['  ,   ', b('text-active')],
    [',   ,     ', b('text-accent')],
    [' ', b('background')],
    [' ', b('background-second')],
    ['  ', b('background-prime')],
    [' ,   ,     ', b('background-accent')],
    ['', b('border')],
    [' ', b('border-contrast')],
];

const Themes = Component.Themes(() => {
    return () => E.div(
        Breadcrumbs.items([['', 'design'], ['']]),
        E.div.class(b())(
            E.div.class(`theme_dark ${b('header')}`)(
                E.div(E.h3(' '))
            ),
            E.div.class(`theme_light ${b('header')}`)(
                E.div(E.h3(' '))
            ),
            example.map(([text, className]) => [
                E.div.class('theme_dark')(E.div.class(className)(text)),
                E.div.class('theme_light')(E.div.class(className)(text)),
            ]),
        )
    );
});


export default Themes;
